















import Vue from 'vue';
import { Prop, Component } from 'vue-property-decorator';

@Component({
  name: 'fields-group',
})
export default class FieldsGroup extends Vue {
  @Prop() public items!: string[];

  public properFieldStyle(index: number): string {
    return index === 0 ? 'first-field' : 'rest-field';
  }
}
